/**
* Media queries
**/
.filters-container .filter .horizontal-line {
  margin: 20px 0;
}
.filters-container .filter h3 {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-btn-tab-1);
  color: var(--clr-smoke-2);
  line-height: 32px;
  letter-spacing: normal;
  margin-bottom: 20px;
}
.filters-container .filter .filter-group ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.filters-container .filter .filter-group li {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 20px;
}
.filters-container .filter .filter-group .checkboxWrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  cursor: pointer;
}
.filters-container .filter .filter-group .checkboxWrapper label {
  font-family: var(--font-family-2);
  font-size: var(--theme-font-h3-mob-4);
  color: var(--clr-smoke-2);
  line-height: normal;
  letter-spacing: normal;
}
.filters-container .filter .filter-group .checkboxWrapper input[type=checkbox] {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 0;
  opacity: 0;
  width: 16px;
  height: 16px;
  z-index: 1;
  border: 1px solid var(--clr-smoke-1);
}
.filters-container .filter .filter-group .checkboxWrapper .checkmark {
  position: relative;
  border: 1px solid var(--clr-smoke-1);
  background-color: transparent;
  pointer-events: none;
  width: 16px;
  height: 16px;
  border-radius: 2px;
  aspect-ratio: 1/1;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 0;
}
.filters-container .filter .filter-group .checkboxWrapper .checkmark svg {
  opacity: 0;
}
.filters-container .filter .filter-group .checkboxWrapper:hover > input[type=checkbox] ~ .checkmark {
  background-color: #ccc;
  border: 1px solid #ccc;
}
.filters-container .filter .filter-group .checkboxWrapper > input[type=checkbox]:checked ~ .checkmark {
  background-color: var(--clr-red);
  border: 1px solid var(--clr-red);
}
.filters-container .filter .filter-group .checkboxWrapper > input[type=checkbox]:checked ~ .checkmark svg {
  opacity: 1;
}
.filters-container .filter .filter-group .checkboxWrapper > input[type=checkbox]:checked ~ label {
  font-family: var(--font-family-2-1);
}
.filters-container .filter .filter-group__buttons {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 11px;
  width: 100%;
}
.filters-container .filter .filter-group__buttons .button.button--outline {
  padding: 5px 0px;
  width: auto;
  border: 2px solid rgba(48, 48, 48, 0.2);
}
.filters-container .filter .filter-group__buttons .button.button--outline span {
  color: var(--clr-smoke-2);
}
.filters-container .filter .filter-group__buttons .button.button--outline:hover span, .filters-container .filter .filter-group__buttons .button.button--outline.active span {
  color: var(--clr-white);
}
.filters-container .filter .filter-group__buttons .button.button--outline:after {
  background: var(--clr-smoke-2);
}
.filters-container .filter .filter-group__buttons .button.button--outline.active:after {
  transform: translate(0);
}
.filters-container .filter .filter-group .range__slider {
  position: relative;
  margin: 40px 0;
  min-height: 2px;
  background-color: rgba(48, 48, 48, 0.2);
}
.filters-container .filter .filter-group .range__track {
  position: absolute;
  height: 100%;
}
.filters-container .filter .filter-group .range__slide-input {
  background: none;
  position: absolute;
  width: 100%;
  pointer-events: none;
  top: 50%;
  transform: translateY(-50%);
  appearance: none;
}
.filters-container .filter .filter-group .range__slide-input[type=range]::-webkit-slider-thumb {
  height: 16px;
  width: 16px;
  border-radius: 50%;
  border: 3px solid var(--clr-red);
  background: var(--clr-red);
  pointer-events: auto;
  appearance: none;
  cursor: pointer;
}
.filters-container .filter .filter-group .range__slide-input[type=range]::-moz-range-thumb {
  height: 16px;
  width: 16px;
  border-radius: 50%;
  border: 3px solid var(--clr-red);
  background: var(--clr-red);
  pointer-events: auto;
  appearance: none;
  cursor: pointer;
}
.filters-container .filter .filter-group .range__number {
  display: flex;
  text-align: left;
  gap: 11px;
}
@media only screen and (max-width: 1439px) {
  .filters-container .filter .filter-group .range__number {
    flex-direction: column;
    gap: 5px;
  }
}
.filters-container .filter .filter-group .range__number label {
  margin-bottom: 8px;
  display: block;
}
.filters-container .filter .filter-group .range__number input {
  padding: 12px 5px 12px 25px;
  width: 100%;
  border: 2px solid rgba(48, 48, 48, 0.2);
  font-family: var(--font-family-2-2);
  font-size: var(--theme-font-btn-mob-1);
  color: var(--clr-black);
  line-height: 24px;
  letter-spacing: normal;
}
.filters-container .filter .filter-clear {
  margin-bottom: 32px;
}
.filters-container .filter .filter-clear button.clear-btn {
  font-family: var(--font-family-2-2);
  font-size: var(--theme-font-h3-mob-4);
  color: var(--clr-smoke-2);
  line-height: 24px;
  letter-spacing: normal;
  text-transform: uppercase;
  background: none;
  border: none;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 12px;
  padding-bottom: 5px;
  position: relative;
  cursor: pointer;
}
@media only screen and (max-width: 1439px) {
  .filters-container .filter .filter-clear button.clear-btn {
    font-size: 11px;
    text-align: left;
    gap: 5px;
  }
}
.filters-container .filter .filter-clear button.clear-btn:after {
  content: "";
  position: absolute;
  width: 0%;
  height: 2px;
  background-color: var(--clr-smoke-2);
  transition: all 0.3s ease-in-out;
  bottom: 0;
  left: 0;
}
.filters-container .filter .filter-clear button.clear-btn:hover:after, .filters-container .filter .filter-clear button.clear-btn.active:after {
  width: 100%;
}
.filters-container .filter .advertisement {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 1258px;
  margin-bottom: 20px;
}
@media only screen and (max-width: 1439px) {
  .filters-container .filter .advertisement {
    height: 400px;
  }
}
@media only screen and (max-width: 991px) {
  .filters-container .filter .advertisement {
    height: 300px;
  }
}
@media only screen and (max-width: 767px) {
  .filters-container .filter .advertisement {
    height: 600px;
  }
}
.filters-container .filter .advertisement:after {
  position: absolute;
  content: "";
  background-color: rgba(0, 0, 0, 0.4117647059);
  width: 100%;
  height: 100%;
  z-index: 0;
  top: 0;
}
.filters-container .filter .advertisement .img {
  position: absolute;
  width: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.filters-container .filter .advertisement .img img {
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.filters-container .filter .advertisement__text {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-bottom: 48px;
  width: 100%;
}
.filters-container .filter .advertisement h3 {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-h2-mob-2-1);
  color: var(--clr-white);
  line-height: 44px;
  letter-spacing: normal;
  margin-bottom: 24px;
  text-align: center;
}
.filters-container #close-btn {
  max-width: none;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  padding: 10px;
  font-size: var(--theme-font-p-mob-5);
}
.filters-container .filter--drawer {
  position: fixed;
  top: 0;
  display: none;
  background-color: var(--clr-white);
  height: 100%;
  max-width: 300px;
  width: 100%;
  opacity: 1;
  transition: transform 0.17s ease-in-out;
  z-index: 9999;
  border-top: 1px solid #ececec;
  transform: translateX(-100%);
  left: 0;
}
@media only screen and (max-width: 991px) {
  .filters-container .filter--drawer {
    display: block;
  }
  .filters-container .filter--drawer.active {
    transform: translateX(0%);
  }
}
.filters-container .filter--drawer .filter-clear {
  margin-bottom: 10px;
}
.filters-container .filter--drawer h3 {
  font-size: 1em;
}
.filters-container .filter--drawer .filter-group li {
  margin-bottom: 10px;
}
.filters-container .filter-wrapper__content {
  height: calc(100% - 120px);
  padding: 12px;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 20px;
}
.filters-container .filter-wrapper__content #close-btn {
  padding: 20px;
}
.filters-container .filter-wrapper__footer {
  position: absolute;
  bottom: 0;
  left: 0;
  border-top: 1px solid var(--clr-black);
  height: fit-content;
  width: 100%;
  padding: 20px;
  background: var(--clr-white);
  z-index: 10000;
}
.filters-container .filter-wrapper__footer #close-btn {
  padding: 10px;
  justify-content: center;
}