/**
* Media queries
**/
.header__top-bar {
  padding: 22px 0;
}
@media only screen and (max-width: 767px) {
  .header__top-bar {
    padding: 12px 0;
    display: none;
  }
}
.header__top-bar .location,
.header__top-bar .settings {
  display: flex;
  align-items: center;
}
.header__top-bar .location {
  gap: 0 12px;
}
.header__top-bar .location:hover .location__text {
  color: var(--clr-red);
}
.header__top-bar .location:hover .location__pin {
  transform: scale(1.1);
}
.header__top-bar .location:hover .location__pin svg path {
  fill: var(--clr-red);
}
@media only screen and (max-width: 767px) {
  .header__top-bar .location {
    justify-content: center;
    margin-bottom: 5px;
  }
}
.header__top-bar .location__pin {
  transition: all 0.6s ease-in-out;
}
.header__top-bar .location__pin svg path {
  transition: all 0.6s ease-in-out;
}
.header__top-bar .location__text {
  font-family: var(--font-family-2);
  font-size: var(--theme-font-p-mob-5);
  color: var(--clr-smoke-2);
  line-height: 20px;
  padding: 0;
  transition: all 0.6s ease-in-out;
}
.header__top-bar .settings {
  justify-content: flex-end;
}
@media only screen and (max-width: 767px) {
  .header__top-bar .settings {
    justify-content: center;
  }
}
.header__top-bar .settings select {
  border: none;
  background-color: transparent;
}
.header__top-bar .settings__currency, .header__top-bar .settings__lang, .header__top-bar .settings__order-track {
  font-family: var(--font-family-2);
  font-size: var(--theme-font-p-mob-5);
  color: var(--clr-smoke-2);
  line-height: 20px;
}
.header__top-bar .settings__order-track:hover a {
  color: var(--clr-red);
}
.header__top-bar .settings__order-track a {
  color: var(--clr-smoke-2);
  transition: all 0.6s ease-in-out;
}