/**
* Media queries
**/
@media only screen and (max-width: 767px) {
  .footer {
    text-align: center;
  }
}
.footer h3 {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-btn-tab-1);
  color: var(--clr-smoke-2);
  line-height: 32px;
  letter-spacing: normal;
  margin-bottom: 16px;
}
.footer ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.footer ul li {
  margin-bottom: 12px;
}
.footer ul li a {
  font-family: var(--font-family-2);
  font-size: var(--theme-font-btn-mob-1);
  color: var(--clr-smoke-2);
  line-height: 24px;
  transition: all 0.3 ease-in-out;
}
.footer ul li a:hover {
  color: var(--clr-red);
}
.footer .footer-block__logo_block {
  max-width: 250px;
}
@media only screen and (max-width: 767px) {
  .footer .footer-block__logo_block {
    margin: 0 auto;
  }
}
.footer .footer-block__logo_block .img--logo {
  margin-bottom: 36px;
}
@media only screen and (max-width: 767px) {
  .footer .footer-block__logo_block .img--logo svg {
    margin: 0 auto;
  }
}
.footer .footer-block__logo_block li a {
  display: flex;
  align-items: flex-start;
  gap: 12px;
}
@media only screen and (max-width: 767px) {
  .footer .footer-block__logo_block li a {
    justify-content: center;
  }
}
.footer .footer-block__logo_block li a .icon svg {
  width: 20px;
  height: 20px;
}
.footer .footer-block__newsletter_block form {
  display: flex;
  align-items: stretch;
  min-height: 64px;
  min-width: 250px;
  margin-bottom: 16px;
}
@media only screen and (max-width: 1199px) {
  .footer .footer-block__newsletter_block form {
    min-width: 0;
  }
}
.footer .footer-block__newsletter_block input {
  width: 100%;
  padding: 0 20px;
  border-right: 1px solid transparent;
  border-left: 1px solid rgba(0, 0, 0, 0.3803921569);
  border-top: 1px solid rgba(0, 0, 0, 0.3803921569);
  border-bottom: 1px solid rgba(0, 0, 0, 0.3803921569);
}
.footer .footer-block__newsletter_block button {
  background-color: var(--clr-red);
  border: 1px solid var(--clr-red);
  min-width: 78px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.5s ease-in-out;
}
@media only screen and (max-width: 1199px) {
  .footer .footer-block__newsletter_block button {
    min-width: 50px;
  }
}
.footer .footer-block__newsletter_block button:hover {
  background-color: var(--clr-white);
}
.footer .footer-block__newsletter_block button:hover svg {
  z-index: 2;
  position: relative;
}
.footer .footer-block__newsletter_block button:hover svg path {
  fill: var(--clr-red);
}
.footer .footer-block__newsletter_block button svg path {
  transition: all 0.5s ease-in-out;
  fill: var(--clr-white);
}
.footer .footer-block__newsletter_block .payment-logos {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  gap: 5px;
}
@media only screen and (max-width: 767px) {
  .footer .footer-block__nav_block {
    margin: 0 auto 30px;
  }
}