/**
* Media queries
**/
.header__logo-bar {
  padding: 22px 0;
}
.header__logo-bar .row {
  align-items: center;
}
.header__logo-bar .location,
.header__logo-bar .settings {
  display: flex;
  align-items: center;
}
.header__logo-bar .logo {
  margin: 0 auto;
}
@media only screen and (max-width: 767px) {
  .header__logo-bar .logo {
    margin: 0 auto 0 0;
  }
}
.header__logo-bar .logo svg,
.header__logo-bar .logo img {
  width: 100%;
  height: auto;
}
@media only screen and (max-width: 991px) {
  .header__logo-bar .logo svg,
  .header__logo-bar .logo img {
    max-width: 80px;
    display: block;
    margin: 0 auto;
  }
}
@media only screen and (max-width: 767px) {
  .header__logo-bar .logo svg,
  .header__logo-bar .logo img {
    margin: 0;
  }
}
.header__logo-bar .icon-group {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.header__logo-bar .icon-group .user {
  display: flex;
  align-items: center;
  gap: 0 16px;
}
.header__logo-bar .icon-group .user__text {
  font-family: var(--font-family-2-1);
  font-size: var(--theme-font-p-mob-1);
  color: var(--clr-smoke-2);
  line-height: 24px;
  text-transform: uppercase;
  opacity: 0.75;
  transition: all 0.5s ease-in-out;
}
.header__logo-bar .icon-group .user svg path {
  transition: all 0.5s ease-in-out;
}
.header__logo-bar .icon-group .user:hover .user__text {
  color: var(--clr-red);
  opacity: 1;
}
.header__logo-bar .icon-group .user:hover svg path {
  fill: var(--clr-red);
}
.header__logo-bar .icon-group .cart__icon svg {
  transition: all 0.3s ease-in-out;
}
.header__logo-bar .icon-group .cart:hover .cart__icon svg {
  transform: scale(1.1);
}
.header__logo-bar .search:hover svg {
  transform: scale(1.1);
}
.header__logo-bar .search form {
  display: flex;
  gap: 12px;
}
.header__logo-bar .search input {
  width: 100%;
  max-width: 236px;
  border: none;
}
.header__logo-bar .search button {
  background-color: transparent;
  border: none;
}
.header__logo-bar .search svg {
  transition: all 0.5s ease-in-out;
}
.header__logo-bar .hamburger {
  width: 30px;
  height: 20px;
  position: relative;
  margin: 0 0 0 auto;
  transform: rotate(0);
  transition: 0.5s ease-in-out;
  cursor: pointer;
}
.header__logo-bar .hamburger span {
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  background: var(--clr-red);
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}
.header__logo-bar .hamburger span:nth-child(1) {
  top: 0px;
}
.header__logo-bar .hamburger span:nth-child(2) {
  top: 8px;
}
.header__logo-bar .hamburger span:nth-child(3) {
  top: 16px;
}
.header__logo-bar .hamburger.open span:nth-child(1) {
  top: 16px;
  transform: rotate(135deg);
}
.header__logo-bar .hamburger.open span:nth-child(2) {
  opacity: 0;
  left: -60px;
}
.header__logo-bar .hamburger.open span:nth-child(3) {
  top: 16px;
  transform: rotate(-135deg);
}