* em-convert is a function of the gulp postcss-em package
*
**/
/**
* Example
**/
/**
* Media queries
**/
.header__nav-bar {
  display: flex;
  background-color: var(--clr-smoke-2);
}
@media only screen and (max-width: 767px) {
  .header__nav-bar {
    background-color: var(--clr-white);
    position: absolute;
    height: 100vh;
    max-width: 65%;
    width: 100%;
    opacity: 1;
    transition: all 0.3s ease-in-out;
    z-index: 9999;
    border-top: 1px solid #ececec;
    transform: translate(100%);
    right: 0;
  }
  .header__nav-bar:after {
    content: "";
    transition: all 0.3s ease-in-out;
    position: absolute;
    opacity: 0;
    transform: translateX(-100%);
    width: 100vw;
    height: 100vh;
    z-index: 9998;
    background-color: rgba(0, 0, 0, 0.5);
    pointer-events: none;
  }
  .header__nav-bar.open {
    transform: translateX(0%);
  }
  .header__nav-bar.open::after {
    opacity: 1;
    pointer-events: auto;
  }
}
.header__nav-bar ul {
  padding: 0;
}
.header__nav-bar .nav.nav--main-menu,
.header__nav-bar .nav.nav--main-menu > li,
.header__nav-bar .nav-phone {
  display: flex;
  align-items: stretch;
}
.header__nav-bar .nav.nav--main-menu,
.header__nav-bar .nav-phone {
  min-height: 88px;
}
@media only screen and (max-width: 991px) {
  .header__nav-bar .nav.nav--main-menu,
  .header__nav-bar .nav-phone {
    min-height: 45px;
  }
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu,
  .header__nav-bar .nav-phone {
    flex-direction: column;
    align-items: center;
    gap: 15px;
    margin-top: 20px;
  }
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu li,
  .header__nav-bar .nav.nav--main-menu a {
    width: 100%;
  }
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu a {
    justify-content: space-between;
  }
}
.header__nav-bar .nav.nav--main-menu li a {
  padding: 0 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
  font-family: var(--font-family-2-1);
  font-size: var(--theme-font-p-mob-1);
  color: var(--clr-white);
  line-height: 24px;
  text-transform: uppercase;
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu li a {
    color: var(--clr-smoke-2);
    font-family: var(--font-family-2);
  }
}
.header__nav-bar .nav.nav--main-menu li a .nav-arrow svg {
  transform-origin: center;
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu li a .nav-arrow svg path {
    fill: var(--clr-smoke-2);
  }
}
.header__nav-bar .nav.nav--main-menu li.open > a > .nav-arrow > svg {
  transform: rotate(-90deg);
}
.header__nav-bar .nav.nav--main-menu li:hover > a, .header__nav-bar .nav.nav--main-menu li.active > a {
  background-color: var(--clr-red);
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu li:hover > a, .header__nav-bar .nav.nav--main-menu li.active > a {
    background-color: transparent;
  }
}
@media only screen and (min-width: 768px) {
  .header__nav-bar .nav.nav--main-menu li.has-submenu:hover > .submenu {
    display: block;
  }
}
.header__nav-bar .nav.nav--main-menu li.has-submenu.open > .submenu {
  display: block;
}
.header__nav-bar .nav.nav--main-menu li.has-submenu .has-submenu:hover li a svg {
  transform: rotate(-90deg);
}
@media only screen and (min-width: 768px) {
  .header__nav-bar .nav.nav--main-menu li.has-submenu .has-submenu:hover > .submenu {
    display: block;
  }
}
.header__nav-bar li.has-submenu {
  position: relative;
}
@media only screen and (max-width: 767px) {
  .header__nav-bar li.has-submenu {
    flex-direction: column;
  }
}
.header__nav-bar li.has-submenu a {
  gap: 5px;
}
.header__nav-bar .nav.nav--main-menu .submenu {
  display: none;
  position: absolute;
  width: fit-content;
  min-width: 100%;
  top: 100%;
  list-style: none;
  padding: 0;
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu .submenu {
    position: inherit;
    width: 100%;
  }
}
.header__nav-bar .nav.nav--main-menu .submenu > li {
  background-color: var(--clr-smoke-2);
  padding: 5px 0;
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu .submenu > li {
    background-color: transparent;
  }
}
.header__nav-bar .nav.nav--main-menu .submenu > li:hover {
  background-color: var(--clr-red);
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu .submenu > li:hover {
    background-color: transparent;
  }
}
.header__nav-bar .nav.nav--main-menu .submenu > li > a {
  padding: 5px 5px 5px 15px;
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu .submenu > li > a {
    padding: 5px 5px 5px 25px;
  }
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu.submenu-depth-0 > li {
    border-bottom: 1px solid #ececec;
  }
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu.submenu-depth-0 > li a {
    padding: 15px 5px;
  }
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu .submenu.submenu-depth-1 > li > a {
    padding: 5px 5px 5px 15px;
  }
}
.header__nav-bar .nav.nav--main-menu .submenu.submenu-depth-2 {
  left: 100%;
  top: 0;
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu .submenu.submenu-depth-2 {
    position: initial;
    left: 0;
  }
}
@media only screen and (max-width: 767px) {
  .header__nav-bar .nav.nav--main-menu .submenu.submenu-depth-2 > li > a {
    padding: 5px 5px 5px 30px;
  }
}
.header__nav-bar .nav-phone {
  justify-content: flex-end;
}
@media only screen and (max-width: 991px) {
  .header__nav-bar .nav-phone__text {
    display: none;
  }
}
.header__nav-bar .nav-phone a {
  padding: 0 30px;
  display: flex;
  align-items: center;
  transition: all 0.3s ease-in-out;
  font-family: var(--font-family-2-1);
  font-size: var(--theme-font-p-mob-1);
  color: var(--clr-white);
  line-height: 24px;
  text-transform: capitalize;
  gap: 13px;
}
.header__nav-bar .nav-phone a:hover, .header__nav-bar .nav-phone a.active {
  background-color: var(--clr-red);
}